.post {
    display: grid;
    grid-template-columns: 0fr 12fr;
    margin-bottom: 20px;
}

.round {
    margin:auto;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    background: #3CA3B8;
}


.likesCount span {
    padding-left: 5px;
}


.root {
    display: flex;
    outline: 0 solid red;
    width: 650px;
}

.deleteIcon {
    font-size: 22px;
    color: #888;
    cursor: pointer;
}

.deleteIcon:hover {
    color: black
}