.header {
    background-color: #19465B;
    width: 100%;
    height: 70px;
    padding-left: 10px;
    margin-bottom: 10px;
    outline: 0px solid red;
    display: flex;
    justify-content: space-between;
    position: relative;
}


.header a{
    float: right;
    margin-right: 20px;
    color: white;
    text-decoration: none;
    margin-top: 20px;
    
}


.header img {
    height: 40px;
    width: 40px;
}

.userPhoto {
    margin-top: 15px;
    margin-right: 20px;
    background: #19465B;
    border: 0;
}

