.nav {
    position: absolute;
    left: 1100px;
    top: 75px; 
    border-radius: 10px;
    background-color: #F8F8F8;
    box-shadow: 0 2px 3px rgb(0 0 0 / 40%);
}

.nav .firstLi {
    margin-top: 10px;
}


.nav div{
    width: 190px;
    height: 30px;
    list-style-type:none;
    color: black;
    font-size: 22px;
    padding-left: 20px;
    border: 1px;
}


.nav div:hover {
    background-color: #E5EBE8;
}   

.nav div.signOut {
    margin-top: 25px;
    margin-bottom: 10px;
    color: red;
    font-size: 22px;;
    cursor: pointer;
    border: 0px;
}

