.userInfo {
    display: flex;
    outline: 0px solid red;
    position: relative;
}

.userInfo img {
    width: 150px;
    height: 150px;
    margin-right: 30px;
    margin-left: 10px;
    margin-top: 15px;
    border-radius: 100px;
}

.photoFile {
    width: 120px;
    margin-left: 18px;
    display: none;
}

.addPhotoStyle{
    color: #3CA3B8;
    padding-left: 30px;
    font-size: 18px;
    cursor: pointer;
}


.addPhotoIcon {
    font-size: 20px;
}