.profileData {
    display: flex; 
}

.aboutUser {
    max-width: 300px;
}


.aboutMe {
    font-weight: bold;
}

.name {
    font-size: 30px;
}

.lookingForAJob {
    font-weight: bold;
}

.lookingForAJobDescription{
    font-weight: bold;
}

.button {
    width: 100px;
    color: white;
    background-color: #00B666;
    margin-top: 10px;
    font-size: 18px;
    transition-duration: 0.4s;
    cursor: pointer;
}

.button:hover {
    background-color: white;
    color: black;
}


.contacts {
    margin-left: 50px;
}

.contacts .title {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 10px;
}



