.form {
    display: flex;
}

.aboutUser {
    outline: 0px solid red;
}

.button {
    margin-bottom: 20px;
    margin-left: 100px;
    cursor: pointer;
    width: 100px;
    color: white;
    background-color: #00B666;
    font-size: 18px;
    transition-duration: 0.4s;
}

.button:hover {
    background-color: white;
    color: black;
}




.field {
    font-weight: 500;
    padding-bottom: 8px;
    display: flex;
}


.field.checkbox input{
    height: 18px;
    width: 18px;
}

.field.inputWidth input {
    width: 200px;
    padding: 5px;
}

.field label {
   margin-right: 20px;
}

.field.field.inputWidth textarea:focus {
    background-color: lightblue;
}
.field.inputWidth input:focus{
    background-color: lightblue;
} 

.contacts{
    margin-left: 30px;
}

.contactTitle {
    font-weight: bold;
    font-size: 20px;
    text-align: center;
    padding-bottom: 10px;
}

.contacts .field {
    padding-left: 20px;
}


.error {
    margin-top: 20px;
}


.closeIcon {
    font-size: 20px;
    margin: 10px;
    cursor: pointer;
}